import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import logo from 'images/logo.svg';
import {Grid, Card, Box, Grow} from "@material-ui/core";

const Home = () => {
  
  return (
<React.Fragment>
	<Box mt={4} />
  	<Grid
			container
			justify='center'
			spacing={2}
			 alignItems="center"
	>
    <Card >
		<Grid container
			justify='center'
			spacing={2}
			 alignItems="center"
		>
		<img 
			width='100%'
			src={logo} 
			padding='50px'
			alt="Heart"
			/>
		</Grid>
	</Card>
	</Grid>
	</React.Fragment>
  );
};

export default Home
